// 
// page-title.scss
//

.page-title-box {
    display: flex;
    justify-content: space-between;
    align-items: center;
    .page-title {
        font-size: 1.25rem;
        margin: 0;
        line-height: 75px;
        color: $gray-900;
    }
    /*.page-title-right {
        float: right;
        margin-top: 22px;
    }*/

    .breadcrumb {
        margin: 0;
    }
}


@include media-breakpoint-down(sm) {
    .page-title-box {
        .page-title {
            display: none;
            white-space: nowrap;
            text-overflow: ellipsis;
            overflow: hidden;
            line-height: 70px;
        }
        /*.breadcrumb {
            display: block;
        }*/
    }
}

@media (max-width: 640px) {
    .page-title-box {
        .page-title-right {
            display: none;
        }
    }
}

